@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.list {
  width: 300px;
  margin: 20px auto;
  height: 50px;
  border-radius: 5px;
}
.list p {
  padding: 10px;
  position: relative;
  margin-bottom: 40px;
  transition: ease-in-out;
}
.list p input {
  background-color: transparent;
  border: 0;
  font-family: 'Manrope', sans-serif;
  font-weight: bold;
  font-size: 20px;
  text-align: left;
  color: #4d535c;
  margin-bottom: 60px;
}
.list p input:focus {
  outline: none;
}
.list p span {
  position: absolute;
  right: 10px;
}
.faicons {
  margin: 0 5px;
  font-size: 15px;
  padding: 10px;
  cursor: pointer;
  color: #4d535c;
}
.itemsAnim-enter {
  opacity: 0.01;
}
.itemsAnim-enter .itemsAnim-enter-active {
  opacity: 1;
  transition: opacity 300ms ease-in;
}
.itemsAnim-leave {
  opacity: 1;
}
.itemsAnim-leave .itemsAnim-leave-active {
  opacity: 0.01;
  transition: opacity 300ms ease-in;
}

.date {
  font-weight: normal;
  color: #b2b3b5;
  font-size: 15px;
  text-align: left;
  margin-bottom: 50px;
  position: relative;
  margin-top: -55px;
  
}


.App {
  padding-right: 30px !important;
  padding-left: 30px !important;
  text-align: center;
}

.App {
  min-height: 500px;
  margin: 40px auto;
}

#to-do-form input {
  background-color: #2925d6;
  border: 0;
  color: #fff;
  height: 50px;
  padding: 0 20px;
  font-size: 16px;
  border-radius: 40px 0 0 40px;
  margin-top: 50px;
  font-family: "Manrope", sans-serif;
}
#to-do-form input::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 1);
  font-family: "Manrope", sans-serif;
  font-weight: bold;
  text-align: center;
}
#to-do-form input:-ms-input-placeholder {
  color: rgba(255, 255, 255, 1);
  font-family: "Manrope", sans-serif;
  font-weight: bold;
  text-align: center;
}
#to-do-form input::-ms-input-placeholder {
  color: rgba(255, 255, 255, 1);
  font-family: "Manrope", sans-serif;
  font-weight: bold;
  text-align: center;
}
#to-do-form input::placeholder {
  color: rgba(255, 255, 255, 1);
  font-family: "Manrope", sans-serif;
  font-weight: bold;
  text-align: center;
}
#to-do-form input:focus {
  outline: none;
}
#to-do-form button {
  height: 50px;
  width: 80px;
  margin-left: -50px;
  border-radius: 0 40px 40px 0;
  border: 0;
  background-color: #2925d6;
  font-family: "Manrope", sans-serif;
  color: #9a98eb;
  margin-bottom: -20px;
  font-size: 18 px;
  font-weight: bold;
  cursor: pointer;
}
#to-do-form button:focus {
  outline: none;
}

.main-box {
  background-color: violet;
}

.listing {
  margin-top: 70px;
  background-color: white;
  padding: 60px;
  border-radius: 10px;
  box-shadow: 0px 0px 36px 33px rgba(228, 229, 237, 1);
}

.todo-title {
  font-family: "Manrope", sans-serif;
  font-weight: bolder;
  font-size: 25px;
  margin-bottom: 50px;
  font-weight: 700;
  color: #333;
}

.message {
  font-family: "Manrope", sans-serif;
  font-size: 20px;
  font-weight: 600;
  color: #777;
}

input[type="text"] {
  padding-right: 50px;
}

.hello {
  font-family: "Manrope", sans-serif;
  font-size: 60px;
  padding-top: 20px;
  font-weight: 800;
  text-align: left;
  font-weight: 800;
  color: #444;
}

.taskCountMessage {
  font-family: "Manrope", sans-serif;
  text-align: left;
  font-size: 24px;
  color: #777;
  padding-top: 30px;
  font-weight: 600;
}

.taskCount {
  color: #2925d6;
}

@media screen and (min-width: 600px) {
  .App {
    width: 500px;
  }
}

